$mobile-size: 750px;
$theme-color: #0b4b6f;

header#header-pc {
  & > ul > li > a {
    color: unset;
    text-decoration: unset;
    font-family: "SCDream7";
    z-index: 1;
  }

  &.float {
    box-shadow: 0 2px 10px #0000001a;
    background: #ffffffcc;
    padding-top: 0px;
    padding-bottom: 0px;
    transition: all 0.3s linear;

    img {
      transition: all 0.3s linear;
    }

    a {
      color: $theme-color;
    }

    .heaher-main-li {
      transition: all 0.3s linear;
      ul {
        background-color: #ffffffd9;

        a {
          color: $theme-color;
        }
      }
    }
  }

  .heaher-main-li.hover {
    // background-color: #003b5dd9;
    transition: all 0.3s linear;

    ul {
      transform: translateX(-50%) scaleY(1);
      visibility: visible;
      transition: all 0.3s linear;
    }
  }
}

@keyframes fade {
  from {
    opacity: 0.8;
  }
  to {
    opacity: 1;
  }
}

@media screen and (max-width: $mobile-size) {
  header#header-pc {
    display: none;
  }
}
