$mobile-size: 750px;

@font-face {
    font-family: "black-hansans";
    font-style: normal;
    font-weight: 200;
    src: url("../assets/font/BlackHanSans-Regular.ttf");
}

@font-face {
    font-family: "SCDream1";
    src: url("../assets/font/SCDream1.otf");
}
@font-face {
    font-family: "SCDream2";
    src: url("../assets/font/SCDream2.otf");
}
@font-face {
    font-family: "SCDream3";
    src: url("../assets/font/SCDream3.otf");
}
@font-face {
    font-family: "SCDream4";
    src: url("../assets/font/SCDream4.otf");
}
@font-face {
    font-family: "SCDream5";
    src: url("../assets/font/SCDream5.otf");
}
@font-face {
    font-family: "SCDream6";
    src: url("../assets/font/SCDream6.otf");
}
@font-face {
    font-family: "SCDream7";
    src: url("../assets/font/SCDream7.otf");
}
@font-face {
    font-family: "SCDream8";
    src: url("../assets/font/SCDream8.otf");
}
@font-face {
    font-family: "SCDream9";
    src: url("../assets/font/SCDream9.otf");
}

html {
    width: 100%;
    min-width: 1000px;
    height: 100%;
    overflow-x: hidden;
}
header {
    width: 100%;
    min-width: 1000px;
}
body {
    width: 100%;
    min-width: 1000px;
    height: 100%;
    user-select: none;
    margin: 0px;
}
body * {
    font-family: "SCDream5", "NanumBarunGothic", sans-serif;
}
#root {
    width: 100%;
    min-width: 1000px;
    text-align: center;
    min-height: 100%;
    position: relative;
}

@media screen and (max-width: $mobile-size) {
    html {
        min-width: 350px;
    }
    header {
        min-width: 350px;
    }
    body {
        min-width: 350px;
    }
    #root {
        min-width: 350px;
    }
}
